.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

.site-layout .site-layout-background {
  background: #fff;
}
[data-theme="dark"] .site-layout .site-layout-background {
  background: #141414;
}
.site-layout-background{
  position: relative;
}

.webmap {
  height: 100vh;
}

.button-notif{
  position: absolute;
  bottom: 20px;
  margin-left: 65px;
}

.button-clear{
  position: absolute;
  bottom: 20px;
  margin-left: 120px;
}

.button-hotspot{
  position: absolute;
  bottom: 20px;
  margin-left: 175px;
}

.spinner{
  position: fixed;
  bottom: 50vh;
  right: 50vw;
}

#timeSlider {
  position: absolute;
  left: 100px;
  right: 100px;
  bottom: 50px;
}

.esri-view-width-xlarge .esri-popup__main-container,
.esri-view-width-large .esri-popup__main-container,
.esri-view-width-medium .esri-popup__main-container
{
